@font-face {
    font-family: PostGrotesk;
    src: url(./fonts/PostGrotesk-Medium.otf);
}


.App {
background: white;
width: 100vw;
height: 100vh;
font-family: PostGrotesk; 
font-size: 25px;
}


#bodyTitle {
 font-family: PostGrotesk;
 -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
 box-sizing: border-box; 
 position: absolute;
 display: -ms-flexbox;
 display: flex;
 padding-left: 4vw;
 padding-top: 1em;
 font-size: 0.75em;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  position: fixed;
  text-align: center;
  width: 400px;
  height: 400px;
  /*top: 50vh;
  left: 50vw;
  transform: translate(-100%,-100%);*/
  left: calc(50vw - 200px);
  top: calc(50vh - 200px);
}

#logo:hover {
  opacity: 0;
}

#logo{
  opacity: 1;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#footerRow {
position: absolute;
bottom: 5vh;
display: -ms-inline-flexbox;
display: inline-flex;
-ms-flex-align: middle;
    align-items: middle;
width: 100vw;
}

#footerContainer {
position: relative;
margin: 0 auto;
font-family: PostGrotesk;
font-size: 0.75em;
/*box-sizing: border-box;*/
-ms-flex-direction: row;
    flex-direction: row;
}


a {
  color: black;
  text-decoration: none;
  padding-left: 1em;
  padding-right: 1em;
}
a:hover {
  color: rgb(100,96,31);
}
a:visited {
  color: black;
}
a:active {
  color: black;
}
a:visited {
  color: black;
}
a:visited:hover {
  color: rgb(100,96,31);
  }



